.livelink-react-ui-component {
    font-family: ui-sans-serif, sans-serif;
    line-height: 1.5;

    font-feature-settings: normal;
    font-variation-settings: normal;
    -webkit-tap-highlight-color: transparent;
    -webkit-font-smoothing: antialiased;
    -webkit-text-size-adjust: 100%;

    color: var(--3dverse-color-content-primary);
}

/* CSS Reset */
.livelink-react-ui-component :where(h1, h2, h3, h4, p, figure, blockquote, dl, dd) {
    margin: 0;
}

.livelink-react-ui-component :where(input, button, textarea, select) {
    font: inherit;
}

.livelink-react-ui-component :where(input, textarea) {
    caret-color: var(--3dverse-color-accent);

    &::selection {
        background-color: var(--3dverse-color-accent);
        color: var(--3dverse-color-content-primary-dark);
    }
}

.livelink-react-ui-component :where(*, *::before, *::after) {
    box-sizing: border-box;
}

/* Reset css variables for light theme */
[data-theme="light"] .livelink-react-ui-component {
    color: var(--3dverse-color-content-primary);
}
