:root,
[data-theme="dark"] {
    /* Background colors */

    --3dverse-color-bg-ground: hsl(227 39% 5%);
    --3dverse-color-bg-underground: hsl(240 20% 2%);
    --3dverse-color-bg-overground: hsl(224 30.61% 9.61%);
    --3dverse-color-bg-foreground: hsl(214.29deg 33.33% 12.35% / 50.2%);

    /* Text colors */

    --3dverse-color-content-primary: hsl(227.44 84.31% 90%);
    --3dverse-color-content-primary-dark: hsl(229 27% 12%);
    --3dverse-color-content-secondary: hsl(231 70% 84%);
    --3dverse-color-content-tertiary: hsl(231 40% 70%);
    --3dverse-color-content-quaternary: hsl(230 18% 34%);

    /* Border colors */

    --3dverse-color-border-primary: hsl(228 19% 40%);
    --3dverse-color-border-secondary: hsl(228 19% 30%);
    --3dverse-color-border-tertiary: hsl(228 21% 13%);
    --3dverse-color-border-quaternary: hsl(227 11% 15%);
    --3dverse-color-border-primary-alpha: hsl(228 21% 40% / 0.5);
    --3dverse-color-border-secondary-alpha: hsl(228 19% 30% / 0.5);
    --3dverse-color-border-tertiary-alpha: hsl(228 21% 20% / 0.5);
    --3dverse-color-border-quaternary-alpha: hsl(228 21% 13% / 0.5);

    /* Accent colors */

    --3dverse-color-accent: var(--3dverse-color-accent-500);
    --3dverse-color-accent-100: hsl(47 85% 78%);
    --3dverse-color-accent-400: hsl(47 90% 60%);
    --3dverse-color-accent-500: hsl(47 100% 50%);
    --3dverse-color-accent-600: hsl(47 100% 45%);
    --3dverse-color-accent-700: hsl(47 90% 16%);
    --3dverse-color-accent-800: hsl(57 180% 6%);


    /* Feedback colors */

    --3dverse-color-feedback-informative: var(--3dverse-color-feedback-informative-500);
    --3dverse-color-feedback-informative-100: hsl(202 100% 84%);
    --3dverse-color-feedback-informative-500: hsl(202 100% 62%);
    --3dverse-color-feedback-informative-800: hsl(203 94% 21%);
    --3dverse-color-feedback-informative-900: hsl(202 50% 25%);

    --3dverse-color-feedback-positive: var(--3dverse-color-feedback-positive-500);
    --3dverse-color-feedback-positive-500: hsl(158 64% 52%);
    --3dverse-color-feedback-positive-800: hsl(157.2 48% 20.4%);
    --3dverse-color-feedback-positive-900: hsl(158 62.5% 9.41%);

    --3dverse-color-feedback-warning: var(--3dverse-color-feedback-warning-500);
    --3dverse-color-feedback-warning-500: hsl(14.4 86% 55.3%);
    --3dverse-color-feedback-warning-800: hsl(12 29.4% 13.3%);

    --3dverse-color-feedback-negative: var(--3dverse-color-feedback-negative-500);
    --3dverse-color-feedback-negative-400: hsl(0 87.8% 67.84%);
    --3dverse-color-feedback-negative-500: hsl(0 95.7% 63.53%);
    --3dverse-color-feedback-negative-600: hsl(0 63% 48%);
    --3dverse-color-feedback-negative-800: hsl(355.52 46.21% 28.43%);

    /* Shadows */

    --3dverse-shadow-xs: 0px 1px 1px oklab(0 0 0 / 0.5);
    --3dverse-shadow-sm: 0 1px 2px 0 oklab(0 0 0 / .5);
    --3dverse-shadow-md: 0 4px 6px -1px oklab(0 0 0 / .5), 0 2px 4px -2px oklab(0 0 0 / .5);
    --3dverse-shadow-lg: 0 10px 15px -3px oklab(0 0 0 / .2), 0 4px 6px -4px oklab(0 0 0 / .5);
    --3dverse-shadow-xl: 0 20px 25px -5px oklab(0 0 0 / .2), 0 8px 10px -6px oklab(0 0 0 / .5);
    --3dverse-shadow-2xl: 0 25px 50px -12px oklab(0 0 0 / .5);
    --3dverse-shadow-3xl: 0 35px 60px -15px oklab(0 0 0 / .5);

    /* Spacing */

    --3dverse-spacing-1: .25rem;
    --3dverse-spacing-2: .5rem;
    --3dverse-spacing-3: .75rem;
    --3dverse-spacing-4: 1rem;
    --3dverse-spacing-5: 1.25rem;
    --3dverse-spacing-6: 1.5rem;
    --3dverse-spacing-7: 1.75rem;
    --3dverse-spacing-8: 2rem;
    --3dverse-spacing-9: 2.25rem;
    --3dverse-spacing-10: 2.5rem;
    --3dverse-spacing-12: 3rem;
    --3dverse-spacing-14: 3.5rem;
    --3dverse-spacing-16: 4rem;
    --3dverse-spacing-20: 5rem;
    --3dverse-spacing-24: 6rem;
    --3dverse-spacing-28: 7rem;
    --3dverse-spacing-32: 8rem;
    --3dverse-spacing-36: 9rem;
    --3dverse-spacing-40: 10rem;
    --3dverse-spacing-44: 11rem;
    --3dverse-spacing-48: 12rem;
    --3dverse-spacing-52: 13rem;
    --3dverse-spacing-56: 14rem;
    --3dverse-spacing-60: 15rem;
    --3dverse-spacing-64: 16rem;
    --3dverse-spacing-72: 18rem;
    --3dverse-spacing-80: 20rem;
    --3dverse-spacing-96: 24rem;

    /* Font size */

    --3dverse-font-size-3xs: .7rem;
    --3dverse-font-size-2xs: .8rem;
    --3dverse-font-size-xs: .85rem;
    --3dverse-font-size-sm: .95rem;
    --3dverse-font-size-md: 1rem;
    --3dverse-font-size-lg: 1.125rem;
    --3dverse-font-size-xl: 1.25rem;
    --3dverse-font-size-2xl: 1.5rem;
    --3dverse-font-size-3xl: 1.875rem;
    --3dverse-font-size-4xl: 2.25rem;
    --3dverse-font-size-5xl: 3rem;
    --3dverse-font-size-6xl: 3.75rem;
    --3dverse-font-size-7xl: 4rem;
    --3dverse-font-size-8xl: 6rem;
    --3dverse-font-size-9xl: 8rem;

    /* Line height */

    --3dverse-leading-3: .75rem;
    --3dverse-leading-4: 1rem;
    --3dverse-leading-5: 1.25rem;
    --3dverse-leading-6: 1.5rem;
    --3dverse-leading-7: 1.75rem;
    --3dverse-leading-8: 2rem;
    --3dverse-leading-9: 2.25rem;
    --3dverse-leading-10: 2.5rem;
    --3dverse-leading-none: 1;
    --3dverse-leading-tight: 1.25;
    --3dverse-leading-snug: 1.375;
    --3dverse-leading-normal: 1.5;
    --3dverse-leading-relaxed: 1.625;
    --3dverse-leading-loose: 2;

    /* Border radius */

    --3dverse-border-radius-sm: 0.125rem;
    --3dverse-border-radius-md: 0.3125rem;
    --3dverse-border-radius-lg: 0.5rem;
    --3dverse-border-radius-xl: 0.75rem;
    --3dverse-border-radius-2xl: 1rem;
    --3dverse-border-radius-3xl: 1.5rem;
}


[data-theme="light"] {
    --3dverse-color-accent: hsl(47.1 100% 63.53%);

    --3dverse-color-bg-ground: hsl(249 0% 100%);
    --3dverse-color-bg-underground: hsl(249 64% 98%);
    --3dverse-color-bg-overground: hsl(47.35 79.38% 93.21%);
    --3dverse-color-bg-foreground: hsl(249 34% 94%);

    --3dverse-color-content-primary: hsl(220 26% 14%);
    --3dverse-color-content-secondary: hsl(218 17% 25%);
    --3dverse-color-content-tertiary: hsl(216 15% 52%);
    --3dverse-color-content-primary-dark: hsl(227.44 84.31% 10%);

    --3dverse-color-border-primary: hsl(249 40% 86%);
    --3dverse-color-border-secondary: hsl(249 40% 91%);
    --3dverse-color-border-tertiary: hsl(249 40% 95%);
    --3dverse-color-border-primary-alpha: hsl(249 40% 86% / 0.5);
    --3dverse-color-border-secondary-alpha: hsl(249 40% 91% / 0.5);
    --3dverse-color-border-tertiary-alpha: hsl(249 40% 95% / 0.5);

    --3dverse-color-feedback-informative: hsl(202 100% 62%);
    --3dverse-color-feedback-positive: hsl(158 64% 52%);
    --3dverse-color-feedback-warning: hsl(14.4 86% 55.3%);
    --3dverse-color-feedback-negative: hsl(0 95.7% 63.53%);
}
