.colorSelector-module__colorSelector_YEuueG__033 {
  gap: var(--3dverse-spacing-2);
  grid-template-columns: repeat(6, 1fr);
  display: grid;
}

.colorSelector-module__colorButton_YEuueG__033 {
  all: unset;
  cursor: pointer;
  border: 1px solid;
  border-radius: 9999px;
  width: 1.75rem;
  height: 1.75rem;
  transition: border-color .22s;
}

@media (width >= 50em) {
  .colorSelector-module__colorButton_YEuueG__033 {
    width: 1.25rem;
    height: 1.25rem;
  }
}

.colorSelector-module__colorButton_YEuueG__033:hover, .colorSelector-module__colorButton_YEuueG__033:active {
  border-color: currentColor;
}

.colorSelector-module__active_YEuueG__033 {
  outline-offset: 2px;
  outline: 2px solid;
}

.colorSelector-module__customColorButton_YEuueG__033 {
  box-sizing: content-box;
  cursor: pointer;
  border: 1px solid #00000030;
  border-radius: 9999px;
  width: 1.75rem;
  height: 1.75rem;
  transition: border-color .22s;
  position: relative;
  overflow: hidden;
}

@media (width >= 50em) {
  .colorSelector-module__customColorButton_YEuueG__033 {
    width: 1.25rem;
    height: 1.25rem;
  }
}

.colorSelector-module__customColorButton_YEuueG__033:hover, .colorSelector-module__customColorButton_YEuueG__033:focus-within {
  border-color: #00000050;
}

.colorSelector-module__colorInput_YEuueG__033 {
  visibility: hidden;
  cursor: pointer;
  width: 300%;
  height: 300%;
  padding: 0;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.colorSelector-module__plus_YEuueG__033 {
  font-size: var(--3dverse-font-size-sm);
  color: #000;
  opacity: .6;
  pointer-events: none;
  position: absolute;
  top: 48%;
  left: 50%;
  transform: translate(-50%, -50%);
}
