.lightPreview-module__lightPreviewContainer_EmC_YG__033 {
  background-color: color-mix(in srgb, var(--3dverse-color-bg-underground) 20%, transparent);
  border-radius: var(--3dverse-border-radius-md);
  opacity: max(.2, var(--intensity));
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  padding-inline: 2rem;
  transition-property: background-color, opacity;
  transition-duration: .22s;
  display: flex;
}

.lightPreview-module__lightPreviewContainer_EmC_YG__033 > span {
  flex-grow: 1;
}

.lightPreview-module__lightPreviewContainer_EmC_YG__033.lightPreview-module__dimmed_EmC_YG__033 {
  mix-blend-mode: luminosity;
  opacity: .2;
}

.lightPreview-module__lightCable_EmC_YG__033 {
  background: linear-gradient(to bottom, transparent -50%, color-mix(in srgb, var(--color) 80%, black));
  opacity: .4;
  width: 1px;
}

.lightPreview-module__outerBox_EmC_YG__033 {
  aspect-ratio: 1;
  border: 1px solid color-mix(in srgb, var(--color) 80%, black);
  width: 3rem;
  height: 3rem;
  box-shadow: inset 0 0 10px 6px oklch(1 0 0 / .2), 0 0 5px 0 oklch(1 0 0 / .4);
  border-radius: 9999px;
  position: relative;
}

.lightPreview-module__innerGlow_EmC_YG__033 {
  background-color: var(--color);
  filter: blur(10px);
  width: 3.5rem;
  height: 3.5rem;
  transform: translate(-50%, -50%) scale(var(--intensity));
  opacity: var(--intensity);
  border-radius: 9999px;
  position: absolute;
  top: 50%;
  left: 50%;
}
