.style-module__container_OiyPSG__033 {
  flex-direction: column;
  align-items: center;
  display: flex;
}

.style-module__spinnerContainer_OiyPSG__033 {
  padding-block: var(--3dverse-spacing-8);
  justify-content: center;
  align-items: center;
  display: flex;
}

.style-module__innerContainer_OiyPSG__033 {
  flex-direction: column;
  display: flex;
}

.style-module__subCategory_OiyPSG__033 {
  flex-grow: 1;
  display: flex;
}

.style-module__subCategoryHeader_OiyPSG__033 {
  align-items: center;
  gap: var(--3dverse-spacing-2);
  flex-grow: 1;
  display: flex;
}

.style-module__subCategoryName_OiyPSG__033 {
  font-size: var(--3dverse-font-size-xs);
  user-select: none;
  font-weight: 500;
}

.style-module__accordion_OiyPSG__033 {
  border-top: 1px solid var(--3dverse-color-border-tertiary-alpha);
}

.style-module__accordionButton_OiyPSG__033 {
  width: 100%;
  padding-inline: var(--3dverse-spacing-2);
  padding-block: var(--3dverse-spacing-1);
  color: var(--3dverse-color-content-tertiary);
}

.style-module__accordionPanel_OiyPSG__033 {
  padding-bottom: var(--3dverse-spacing-2);
}

.style-module__formControl_OiyPSG__033 {
  font-size: var(--3dverse-font-size-sm);
  border-radius: var(--3dverse-border-radius-sm);
  cursor: pointer;
  flex-direction: row;
  align-items: center;
  padding-right: 3px;
  transition: background-color .22s;
  display: flex;
}

.style-module__formControl_OiyPSG__033:hover, .style-module__formControl_OiyPSG__033:focus {
  background-color: var(--3dverse-color-bg-foreground);
}

.style-module__formLabel_OiyPSG__033 {
  text-align: start;
  padding-inline: var(--3dverse-spacing-2);
  padding-block: var(--3dverse-spacing-1);
  font-size: var(--3dverse-font-size-xs);
  letter-spacing: .02em;
  text-transform: capitalize;
  text-overflow: ellipsis;
  -webkit-line-clamp: var(--line-clamp);
  --line-clamp: 1;
  cursor: pointer;
  user-select: none;
  -webkit-box-orient: vertical;
  flex-grow: 1;
  display: -webkit-box;
  overflow: hidden;
}

.style-module__formControlWrapper_OiyPSG__033 {
  align-items: center;
  gap: var(--3dverse-spacing-2);
  flex-shrink: 0;
  display: flex;
}

.style-module__resetButton_OiyPSG__033 {
  width: var(--3dverse-spacing-5);
  min-width: auto;
  color: var(--3dverse-color-content-tertiary);
}

.style-module__resetAllButton_OiyPSG__033 {
  width: max-content;
  margin-inline: var(--3dverse-spacing-1);
  margin-right: var(--3dverse-spacing-2);
  align-self: end;
}
