.style-module__container_N2j2sa__033 {
  justify-content: center;
  align-items: center;
  gap: var(--3dverse-spacing-3);
  background-color: var(--3dverse-color-bg-overground);
  flex-direction: column;
  width: 100%;
  height: 100%;
  display: flex;
  position: absolute;
}

.style-module__glowEffectStyle_N2j2sa__033:before {
  content: "";
  aspect-ratio: 1;
  mix-blend-mode: screen;
  background: radial-gradient(var(--3dverse-color-bg-overground), transparent);
  filter: blur(50px);
  pointer-events: none;
  z-index: 0;
  border-radius: 100%;
  width: 150%;
  max-width: 12rem;
  position: absolute;
  top: 50%;
  left: 50%;
  translate: -50% -50%;
}

@media (width >= 80em) {
  .style-module__glowEffectStyle_N2j2sa__033:before {
    max-width: 18rem;
  }
}

.style-module__logoContainer_N2j2sa__033 {
  width: 1.75rem;
  color: var(--3dverse-color-accent);
  filter: drop-shadow(0 18px 2px color-mix(in srgb, var(--3dverse-color-bg-ground) 50%, transparent));
}

@media (width >= 80em) {
  .style-module__logoContainer_N2j2sa__033 {
    width: 2rem;
  }
}

.style-module__loaderProgressBarIndeterminateContainer_N2j2sa__033 {
  background-color: var(--3dverse-color-bg-foreground);
  border-radius: 2rem;
  width: 3rem;
  padding: 7px 8px;
}

.style-module__loaderProgressBarIndeterminateTrack_N2j2sa__033 {
  background-color: var(--3dverse-color-bg-underground);
  border-radius: 1rem;
  width: 100%;
  height: 1px;
  position: relative;
  overflow: hidden;
}

.style-module__loaderProgressBarIndeterminateTrack_N2j2sa__033:before {
  content: "''";
  will-change: left;
  background-color: var(--3dverse-color-accent);
  border-radius: 1px;
  min-width: 50%;
  height: 100%;
  animation: 1s infinite style-module__loader-progress-bar-indeterminate_N2j2sa__033;
  position: absolute;
}

.style-module__loaderProgressBarIndeterminateTrack_N2j2sa__033:after {
  content: "";
  will-change: left;
  background-color: var(--3dverse-color-accent);
  opacity: .4;
  border-radius: 1px;
  min-width: 75%;
  height: 100%;
  animation: 1.5s .25s infinite style-module__loader-progress-bar-indeterminate_N2j2sa__033;
  position: absolute;
}

@keyframes style-module__loader-progress-bar-indeterminate_N2j2sa__033 {
  0% {
    left: -40%;
  }

  100% {
    left: 100%;
  }
}

.style-module__label_N2j2sa__033 {
  color: var(--3dverse-color-content-tertiary);
  font-size: var(--3dverse-font-size-xs);
  letter-spacing: .05em;
  user-select: none;
  font-weight: 500;
}
