.style-module__container_BHEzva__033 {
  --size: 81px;
  flex-direction: column;
  align-items: center;
  display: flex;
}

.style-module__circleShadow_BHEzva__033 {
  width: var(--size);
  aspect-ratio: 1;
  pointer-events: none;
  z-index: -10;
  border-radius: 100%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  box-shadow: inset 0 0 10px #665ee160;
}

.style-module__movingLightHint_BHEzva__033 {
  width: var(--size);
  aspect-ratio: 1;
  border: 2px solid var(--3dverse-color-accent);
  filter: blur(2px);
  -webkit-mask-composite: destination-in;
  pointer-events: none;
  border-radius: 100%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  -webkit-mask-image: radial-gradient(circle, #000 20%, #0000 50%);
  mask-image: radial-gradient(circle, #000 20%, #0000 50%);
  -webkit-mask-position: -100px -100px;
  mask-position: -100px -100px;
  -webkit-mask-size: 100%;
  mask-size: 100%;
  -webkit-mask-repeat: no-repeat;
  mask-repeat: no-repeat;
  -webkit-mask-composite: source-in;
  mask-composite: intersect;
}

.style-module__skeleton_BHEzva__033 {
  aspect-ratio: 1;
  background-color: var(--3dverse-color-bg-foreground);
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  margin: 11px;
  animation: 1.5s infinite style-module__pulseAnimation_BHEzva__033;
  display: flex;
}

.style-module__skeleton_BHEzva__033 .style-module__icon_BHEzva__033 {
  width: 24px;
  height: 24px;
  color: var(--3dverse-color-content-tertiary);
  stroke: var(--3dverse-color-bg-foreground);
  stroke-width: 24px;
  opacity: .25;
}

@keyframes style-module__pulseAnimation_BHEzva__033 {
  0%, 100% {
    opacity: 1;
  }

  50% {
    opacity: .5;
  }
}
